import { Rating } from '@material-ui/lab'
import { observer } from 'mobx-react-lite'
import { SnapshotIn } from 'mobx-state-tree'
import React, { useEffect, useState } from 'react'
import { AiOutlineShoppingCart } from 'react-icons/ai'
import { BsTrash } from 'react-icons/bs'
import { MdAddCircleOutline, MdRemoveCircleOutline } from 'react-icons/md'
import { useHistory } from 'react-router'
import Page from '../../components/Page'
import { SubHeader } from '../../components/SubHeader'
import { RUTAS } from '../../constants'
import { formatCantidadCarrito } from '../../helpers'
import { formatTextoURL, useToaster, useVerificarLoginEdad } from '../../helpers-ts'
import { ICONOS } from '../../helpers-ts/iconos'
import { iProducto } from '../../models/favorito-store/favorito-store'
import { useStores } from '../../models/root-store/root-store-context'
import { ProductoCarrito } from '../../models/types/ProductoCarrito'
import './Favoritos.css'
import './Pedido.css'
interface RenderProductoProps {
  producto: iProducto
}

interface iProductoCarrito extends SnapshotIn<typeof ProductoCarrito> {}

interface ModalFavoritosProps {
  show: boolean
  onDismiss: any
  movil: boolean
}

export const Favoritos: React.FC<ModalFavoritosProps> = observer((props) => {
  const rootStore = useStores()
  const { favoritoStore } = rootStore
  const CANTIDAD_PAGINA = 5
  const [CantidadMostrar, setCantidadMostrar] = useState(CANTIDAD_PAGINA)

  return (
    <>
      <Page login_required={false} solo_cliente>
        <SubHeader links={[{ titulo: 'Favoritos' }]} />
        <div
          style={{
            paddingTop: 30,
            paddingBottom: 30,
            width: '100%',
            minHeight: 500,
            height: 'fit-content',
            display: 'flex',
            alignItems: 'center',
            flexDirection: 'column',
          }}
          className="container-lg"
        >
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: 60 }}>
            <strong>
              <h3>Favoritos</h3>
            </strong>
          </div>
          {favoritoStore.productos.length > 0 && (
            <>
              {favoritoStore.productos.slice(0, CantidadMostrar).map((producto: any) => (
                <div style={props.movil ? { width: '100%' } : { width: '100%' }}>
                  <RenderProducto producto={producto} />
                </div>
              ))}
            </>
          )}
          <div className="d-flex justify-content-center m-5">
            {CantidadMostrar < favoritoStore.productos.length && (
              <a
                onClick={() => {
                  setCantidadMostrar(CantidadMostrar + CANTIDAD_PAGINA)
                }}
                style={{ fontWeight: 'bold', color: 'var(--naranja-abi)' }}
              >
                + Ver más
              </a>
            )}
          </div>
          {favoritoStore.cantidadFavoritos === 0 && (
            <div style={{ flex: 1, display: 'flex', justifyContent: 'center' }}>
              <div style={{ color: 'var(--gris-oscuro-abi)' }}>No posee favoritos</div>
            </div>
          )}
        </div>
      </Page>
    </>
  )
})

const RenderProducto: React.FC<RenderProductoProps> = observer((props) => {
  const producto = props.producto
  const rootStore = useStores()

  const carritoDefault = {
    id: producto.id,
    fabricante: producto.fabricante.nombre,
    nombre: producto.nombre,
    descripcion: producto.descripcion,
    precio: producto.precio,
    precio_mayorista: producto.precio_mayorista,
    cantidad_disponible: producto.cantidad_disponible,
    imagenes: producto.imagenes
      ? producto.imagenes[0]?.imagen
        ? producto.imagenes[0].imagen
        : 'assets/abi-logo-producto.png'
      : 'assets/abi-logo-producto.png',
    categoria: producto.categoria?.nombre,
    cantidad: 1,
  }
  const [productoCarrito, setProductoCarrito] = useState(carritoDefault as iProductoCarrito)
  const { favoritoStore, carritoStore, loginStore } = rootStore
  const [cantidadCarrito, setCantidadCarrito] = useState(1)
  const [AlertBloqueoTitle, setAlertBloqueoTitle] = useState<string>('')
  const [AlertBloqueoMessage, setAlertBloqueoMessage] = useState<string>('')
  const [AlertBloqueoAction, setAlertBloqueoAction] = useState<any>(null)
  const [AlertBloqueo, setAlertBloqueo] = useState<boolean>(false)

  const Movil = /Mobi/.test(navigator.userAgent)
  const imagenProducto = producto.imagenes
    ? producto.imagenes.length > 0
      ? producto?.imagenes[0].imagen
      : 'assets/abi-logo-producto.png'
    : 'assets/abi-logo-producto.png'
  const history = useHistory()
  const addToast = useToaster()
  const verificarEdadEstado = useVerificarLoginEdad()

  const toggleFavorito = (producto: iProducto) => {
    const fav = favoritoStore.favorito(producto)
    if (!!fav) {
      const estado = fav.toggleActivo()
      if (fav.id) {
        rootStore.environment.api.updateFavorito(fav.id, estado).then((result) => {
          if (result.kind === 'ok') {
            console.log(result)
          }
        })
      }
    } else {
      favoritoStore.addFavorito(producto)
      rootStore.environment.api.addFavorito(producto.id).then((result) => {
        if (result.kind === 'ok') {
          rootStore.environment.api.getFavoritos().then((result) => {
            if (result.kind === 'ok') {
              favoritoStore.setFavoritosAPI(result.data)
            }
          })
        }
      })
    }
  }

  useEffect(() => {
    setProductoCarrito({
      id: producto.id,
      fabricante: producto.fabricante.nombre,
      nombre: producto.nombre,
      descripcion: producto.descripcion,
      precio: producto.precio,
      precio_mayorista: producto.precio_mayorista,
      cantidad_disponible: producto.cantidad_disponible,
      imagenes: producto.imagenes
        ? producto.imagenes[0]?.imagen
          ? producto.imagenes[0].imagen
          : 'assets/abi-logo-producto.png'
        : 'assets/abi-logo-producto.png',
      categoria: producto.categoria?.nombre,
      cantidad: cantidadCarrito,
    })
  }, [cantidadCarrito])

  const validarCantidadAgregarCarrito = (producto: iProducto, cantidad: any) => {
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    if (obj) {
      if (obj.cantidad + cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    } else {
      if (cantidad > producto.cantidad_disponible) {
        addToast(`No hay suficiente cantidad en stock. Cantidad disponible: ${producto.cantidad_disponible}.`)
        return false
      }
    }
    return true
  }

  const addProductoCarrito = (producto: iProducto, cantidad: any) => {
    if (producto.no_apto_menores) {
      const estado = verificarEdadEstado()
      if (estado.case !== 10) {
        setAlertBloqueoTitle(estado.title)
        setAlertBloqueoMessage(estado.message)
        setAlertBloqueoAction(estado.action)
        setAlertBloqueo(true)
        return false
      }
    }
    let obj = carritoStore.productos.find((o) => o.id === producto.id)
    let productoCarrito
    if (obj) {
      for (let i = 0; i < cantidad; i++) {
        carritoStore.sumarProducto(producto)
      }
    } else {
      productoCarrito = {
        ...producto,
        cantidad: cantidad,
        fabricante: producto.fabricante.nombre,
        imagenes: producto.imagenes ? (producto.imagenes.length > 0 ? producto.imagenes[0].imagen : '') : '',
        categoria: producto.categoria?.nombre,
      }
      carritoStore.addProducto(productoCarrito)
    }
    if (loginStore.isLoggedIn) {
      rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, cantidad).then((result) => {
        if (result.kind === 'ok') {
        }
      })
    }
    setCantidadCarrito(1)
  }

  return (
    <div
      style={{
        display: 'flex',
        width: '100%',
        justifyContent: 'space-between',
        paddingLeft: '5%',
        height: 110,
        textOverflow: 'ellipsis',
        paddingRight: '5%',
        paddingBottom: 10,
        borderBottom: '1px solid var(--gris-claro-abi)',
        marginTop: 10,
      }}
    >
      <div style={{ height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <img
          src={imagenProducto}
          className={Movil ? 'imagen-producto-carrito-fav-movil' : 'imagen-producto-carrito-fav'}
        />
      </div>

      <div style={{ display: 'flex', flexDirection: 'column', flex: 1, paddingLeft: Movil ? 0 : 30 }}>
        <div>
          <Rating name="read-only" size={'small'} value={producto.fabricante.rating} readOnly />
        </div>
        <div style={{ overflow: 'hidden' }}>
          <span
            style={{
              fontWeight: 'bolder',
              fontSize: Movil ? 12 : 15,
              height: Movil ? 26 : 32,
              textOverflow: 'ellipsis',
            }}
          >
            {producto.nombre}
          </span>
        </div>

        <a onClick={() => history.push(RUTAS.PRODUCTO_DETAIL(producto.id, formatTextoURL(producto.nombre)))}>
          <span style={{ color: 'var(--naranja-abi)', fontSize: '0.875rem', fontWeight: 400 }}>Ver detalles</span>
        </a>
      </div>
      <div
        className={'agregar-container'}
        style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', gap: Movil ? 15 : 25 }}
      >
        <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <div
            onClick={() => {
              if (cantidadCarrito > 0) {
                setCantidadCarrito(cantidadCarrito - 1)
              }
            }}
          >
            <MdRemoveCircleOutline size={Movil ? 20 : 30} className="icono-remove" />
          </div>

          <span
            style={{
              marginLeft: 10,
              marginRight: 10,
              fontSize: Movil ? 16 : '1.5rem',
              color: 'var(--gris-claro-abi)',
            }}
          >
            <span
              className={'cart-item cantidad-agregar cantidad-agregar-' + producto.id}
              data-cantidadagregar={formatCantidadCarrito(cantidadCarrito)}
            />
            {formatCantidadCarrito(cantidadCarrito)}
          </span>

          <div onClick={() => setCantidadCarrito(cantidadCarrito + 1)}>
            <MdAddCircleOutline size={Movil ? 20 : 30} className="icono-add" />
          </div>
        </div>
        <button
          className={Movil ? 'button-comprar-carrito-fav-movil' : 'button-comprar-carrito-fav'}
          onClick={(evt: any) => {
            if (!validarCantidadAgregarCarrito(producto, cantidadCarrito)) {
              return
            }

            const cagregar = evt.target.closest('.agregar-container').getElementsByClassName('cantidad-agregar')[0]
            const icarrito = document.getElementById('icon-carrito') as any
            cagregar.classList.add('sendtocart')
            setTimeout(() => {
              cagregar.classList.remove('sendtocart')
              //
              icarrito.classList.add('shake')
              setTimeout(function () {
                icarrito.classList.remove('shake')
                addProductoCarrito(producto, cantidadCarrito)
              }, 500)
            }, 500)
          }}
        >
          <div>
            <span className="carrito-pedido">{ICONOS.carrito}</span>
            {Movil ? null : <span style={{ marginLeft: 5, fontSize: Movil ? 12 : 16 }}>Comprar</span>}
          </div>
        </button>
        <button style={{ backgroundColor: '#fff' }} onClick={() => toggleFavorito(producto)}>
          <BsTrash size={Movil ? 20 : 30} className="icono-remove" />
        </button>
      </div>
    </div>
  )
})

export default Favoritos
